// components/Header.jsx
"use client";

import { Mail, MapPin } from "lucide-react";

export default function Header() {
  return (
    <header className="w-full bg-[#480202] text-white text-sm py-2 px-4">
      <div className="max-w-7xl mx-auto flex flex-col sm:flex-row sm:items-center sm:justify-start gap-2 sm:gap-6">
        <div className="flex items-center gap-2">
          <Mail size={16} className="text-white" />
          <a
            href="mailto:info@sri-lions.com"
            className="hover:underline hover:text-gray-200"
          >
            info@sri-lions.com
          </a>
        </div>

        <div className="flex items-center gap-2">
          <MapPin size={16} className="text-white" />
          <span>No. 4, Lumbini Sports Complex, Colombo, Sri Lanka</span>
        </div>
      </div>
    </header>
  );
}
